<template>
  <div class="check_tab_content">
    <div class="tab_class" :class="$store.state.tabStyle">
      <el-table
        ref="multipleTable"
        tooltip-effect="dark"
        style="width: 100%"
        border
        stripe
        :data="tableDataComputed"
        :class="tabClass"
        @selection-change="handleSelectionChange"
        @row-click="rowClick"
      >
        <el-table-column type="selection" width="50"></el-table-column>
        <el-table-column label="序号" width="50" type="index" sortable></el-table-column>
        <el-table-column prop="fileName" label="文件名称" sortable min-width="110"></el-table-column>
        <el-table-column prop="keyWords" label="命中关键词" sortable min-width="120"></el-table-column>
        <el-table-column prop="hitRate" label="关键词命中率" sortable min-width="130"></el-table-column>
        <el-table-column prop="classified" label="涉密情况 " sortable min-width="110"></el-table-column>
        <el-table-column prop="fileType" label="文件类型" sortable min-width="110"></el-table-column>
        <el-table-column prop="filePath" label="文件路径" sortable min-width="110"></el-table-column>
        <el-table-column prop="moveStorage" label="是否移动存储" sortable min-width="130"></el-table-column>
        <el-table-column prop="fileSize" label="文件大小" sortable min-width="110"></el-table-column>
        <el-table-column prop="createTime" label="创建时间" sortable min-width="110"></el-table-column>
        <el-table-column prop="updateTime" label="修改时间" sortable min-width="110"></el-table-column>
        <el-table-column prop="judge" label="密级初判人" sortable min-width="120"></el-table-column>
      </el-table>
    </div>
    <div class="paging">
      <!-- 分页器 -->
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :current-page="currentPage"
        :page-sizes="[50, 100, 150, 200]"
        :page-size="pagesize"
        :total="tableData.length"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </div>
    <!-- 下拉框 -->
    <div class="select_class">
      <el-select v-model="value1" placeholder="批量密级判断">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-select v-model="value2" placeholder="打开文件">
        <el-option
          v-for="item in options2"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
