          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-button>复制选中摘要</el-button>
    </div>
    <!--  :base-margin-top="baseMarginTop" :base-width="baseWidth" :base-height="baseHeight" :resizable="resizable" :resize-end="resizeEnd" :resize-start="resizeStart" :resizing="resizing" :drag-start="dragStart" :dragging="dragging" :drag-end="dragEnd" -->
    <!-- <vue-power-drag :draggable="draggable" :your-list="list"> -->
